nav {
  width: 100%;
  font-size: 3rem;
  color: #fff;
  /* margin-bottom: 50px; */
  margin-bottom: 0px;
  background-color: rgb(56, 56, 56);
  height: auto;
  font-family: "sans-serif";
  /* position: fixed; */
}

a {
  color: white;
}

a:hover {
  color: white;
  text-decoration: none;
}

nav li {
  display: inline-block;
  padding: 26px 0;
  width: 25%;
}

ul {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 750px) {
  #cur-sco,
  #top-sco {
    font-size: 2rem;
  }
}
